<template>
  <div v-if="loading">
    <div class="d-flex justify-content-center">
      <div
        class="spinner-grow text-danger"
        style="margin-top: 15%"
        role="status"
      >
        <span class="sr-only">Loading...</span>
      </div>
    </div>
  </div>
  <div class="container mt-5 mb-5" v-else :key="index">
    <div class="row">
      <div class="col-md-4">
        <img class="music-cover" v-bind:src="image" alt="Music Cover" />
      </div>
      <div
        class="col-md-8"
        style="margin-top: 55px; font-size: 1.5rem; font-weight: bold"
      >
        <h3 style="text-align: center">{{ title }}</h3>
        <h5 style="text-align: center">{{ artist }}</h5>
        <center>
          <audio controls autoplay class="mt-5" @ended="onEnd()">
            <source v-bind:src="music" type="audio/mpeg" />
            Your browser does not support the audio element.
          </audio>
        </center>
        <center>
          <router-link :to="{ name: 'music', params: { id: +id + +2 } }">
            <button
              type="button"
              class="btn btn-dark mt-3 mb-5"
              style="margin-left: 20px"
            >
              بعدی
            </button>
          </router-link>
          <router-link :to="{ name: 'music', params: { id: +id - +2 } }">
            <button type="button" class="btn btn-dark mt-3 mb-5">قبلی</button>
          </router-link>

          <button
            type="button"
            class="btn btn-dark mt-3 mb-5 mr-4"
            v-if="myrepeat"
            v-on:click="disarepeat()"
          >
            <i class="bi bi-arrow-repeat"></i>
          </button>

          <button
            type="button"
            class="btn btn-dark mt-3 mb-5 mr-4"
            v-on:click="repeat()"
            v-else
          >
            <i class="bi bi-arrow-left-right"></i>
          </button>
        </center>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { ref } from "vue";
import { useRoute } from "vue-router";
import router from "../router";

export default {
  setup() {
    const image = ref([]);
    const title = ref([]);
    const artist = ref([]);
    const music = ref([]);
    const loading = ref(true);
    const myrepeat = ref(false);
    const route = useRoute();
    const id = route.params.id;

    function repeat() {
      myrepeat.value = true;
      console.log(myrepeat.value);
    }

    function disarepeat() {
      myrepeat.value = false;
      console.log(myrepeat.value);
    }

    function onEnd() {
      if (myrepeat.value == false) {
        router.push({ name: "music", params: { id: +id + +2 } });
      } else {
        location.reload();
      }
    }

    function getPosts() {
      axios
        .get(
          `https://one-api.ir/radiojavan/?token=391485:61f7f4d9433398.65027587&action=get_song&id=${id}`
        )
        .then(function (response) {
          image.value = response.data.result.photo;
          title.value = response.data.result.title;
          artist.value = response.data.result.artist;
          music.value = response.data.result.link;
          loading.value = false;
        })
        .catch(function (error) {
          console.log(error);
        });
    }

    getPosts();

    return {
      image,
      title,
      artist,
      music,
      loading,
      route,
      id,
      onEnd,
      repeat,
      myrepeat,
      disarepeat,
    };
  },
};
</script>

