<template>
  <div v-if="loading">
    <div class="d-flex justify-content-center">
      <div
        class="spinner-grow text-danger"
        style="margin-top: 15%"
        role="status"
      >
        <span class="sr-only">Loading...</span>
      </div>
    </div>
  </div>
  <div class="container mt-5" v-else>
    <video controls autoplay style="width: 100%">
      <source v-bind:src="video" type="video/mp4" />
      Your browser does not support the video tag.
    </video>
    <div class="row mt-5 mb-5">
      <div class="col-md-4">
        <img class="music-cover" v-bind:src="image" alt="Music Cover" />
      </div>
      <div
        class="col-md-8"
        style="
          margin-top: 55px;
          font-size: 1.5rem;
          font-weight: bold;
        "
      >
        <h1 style="text-align: center">{{ title }}</h1>
        <h4 style="text-align: center">{{ artist }}</h4>
        <center>
          <audio controls class="mt-5 mb-5" @ended="onEnd()">
            <source v-bind:src="music" type="audio/mpeg" />
            Your browser does not support the audio element.
          </audio>
        </center>
        <center>
          <router-link :to="{ name: 'video', params: { id: +id + +2 } }">
            <button
              type="button"
              class="btn btn-dark mt-3 mb-5"
              style="margin-left: 20px"
            >
              بعدی
            </button>
          </router-link>
          <router-link :to="{ name: 'video', params: { id: +id - +2 } }">
            <button type="button" class="btn btn-dark mt-3 mb-5">قبلی</button>
          </router-link>
        </center>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { ref } from "vue";
import { useRoute } from "vue-router";
import router from "../router";

export default {
  methods: {},

  setup() {
    const image = ref([]);
    const title = ref([]);
    const artist = ref([]);
    const music = ref([]);
    const video = ref([]);
    const loading = ref(true);
    const route = useRoute();
    const id = route.params.id;

    function onEnd() {
      router.push({ name: "video", params: { id: +id + +2 } });
    }

    function getPosts() {
      axios
        .get(
          `https://one-api.ir/radiojavan/?token=391485:61f7f4d9433398.65027587&action=get_video&id=${id}`
        )
        .then(function (response) {
          image.value = response.data.result.photo;
          title.value = response.data.result.title;
          artist.value = response.data.result.artist;
          music.value = response.data.result.link;
          video.value = response.data.result.hq_link;
          loading.value = false;
          console.log(response.data.result);
        })
        .catch(function (error) {
          console.log(error);
        });
    }

    getPosts();

    return {
      image,
      title,
      artist,
      music,
      video,
      loading,
      route,
      id,
      onEnd,
    };
  },
};
</script>

