<template>
  <div v-if="loading">
    <div class="d-flex justify-content-center">
      <div
        class="spinner-grow text-danger"
        style="margin-top: 15%"
        role="status"
      >
        <span class="sr-only">Loading...</span>
      </div>
    </div>
  </div>
  <div class="container mt-1" v-else>
    <div class="row">
      <div class="col-md-3 mt-5" v-for="post in posts" :key="post">
        <router-link
          :to="{ name: 'video', params: { id: post.id } }"
          style="color: black"
        >
          <img
            class="music-cover"
            v-bind:src="post.photo"
            alt="Music Cover"
            @load="onImgLoad"
          />
        </router-link>
        <p class="Music-artist mt-2">{{ post.artist }}</p>
        <router-link
          :to="{ name: 'video', params: { id: post.id } }"
          style="color: black"
        >
          <p class="Music-title">{{ post.song }}</p>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { ref } from "vue";
import { useRoute } from "vue-router";

export default {
  setup() {
    const posts = ref([]);
    const loading = ref(true);
    const route = useRoute();

    function getPosts() {
      axios
        .get(
          "https://one-api.ir/radiojavan/?token=391485:61f7f4d9433398.65027587&action=hot_videos"
        )
        .then(function (response) {
          posts.value = response.data.result;
          loading.value = false;
        })
        .catch(function (error) {
          console.log(error);
        });
    }

    getPosts();

    return { posts, loading, route };
  },
};
</script>

