<template>
  <div class="container mt-5">
    <div class="row">
      <img
        src="../../public/images/undraw_compose_music_ovo2.svg"
        style="width: 50%; float: left"
      />

      <div class="col-md-6 mt-5">
        <h5 class="mt-5" style="font-weight: bold; text-align: right">
          جستجو بین تمام آهنگ و موزیک ویدیو ها
        </h5>
        <form @submit.prevent="validate" class="mt-5">
          <div class="mb-3">
            <input
              type="text"
              class="form-control"
              v-model.lazy.trim="form.title"
              placeholder="جستجو"
            />
            <div class="form-text text-danger">
              {{ form.titleErrorText }}
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>

  <div v-if="loading">
    <div class="d-flex justify-content-center">
      <div
        class="spinner-grow text-danger"
        style="margin-top: 15%"
        role="status"
      >
        <span class="sr-only">Loading...</span>
      </div>
    </div>
  </div>
  <div class="container mt-5" v-else>
    <div class="row">
      <div class="col-md-6">
        <h3 style="text-align: right" v-if="found">آهنگ ها</h3>

        <div class="row">
          <div class="col-md-6 mt-3" v-for="post in posts" :key="post">
            <router-link
              :to="{ name: 'music', params: { id: post.id } }"
              style="color: black"
            >
              <img
                class="music-cover"
                v-bind:src="post.photo"
                alt="Music Cover"
                @load="onImgLoad"
              />
            </router-link>
            <p class="Music-artist mt-2">{{ post.artist }}</p>
            <router-link :to="{ name: 'music', params: { id: post.id } }">
              <p class="Music-title">{{ post.song }}</p>
            </router-link>
          </div>
        </div>
      </div>
      <div class="col-md-6">
        <h3 style="text-align: right" v-if="found">موزیک ویدیو ها</h3>
        <div class="row">
          <div class="col-md-6 mt-3" v-for="post in videos" :key="post">
            <router-link
          :to="{ name: 'video', params: { id: post.id } }"
          style="color: black"
        >
          <img
            class="music-cover"
            v-bind:src="post.photo"
            alt="Music Cover"
            @load="onImgLoad"
          />
        </router-link>
            <p class="Music-artist mt-2">{{ post.artist }}</p>
            <router-link :to="{ name: 'video', params: { id: post.id } }">
              <p class="Music-title">{{ post.song }}</p>
            </router-link>
          </div>
        </div>
      </div>
    </div>
  </div>

  <div class="d-flex justify-content-center mt-5" v-if="notfound">
    <h3 style="color: red">آهنگ موردنظر یافت نشد</h3>
  </div>
</template>

<script>
import { reactive, ref } from "vue";
import axios from "axios";

export default {
  setup() {
    const posts = ref([]);
    const videos = ref([]);

    const form = reactive({
      title: "",
      titleErrorText: "",
    });
    const loading = ref(false);
    const notfound = ref(false);
    const found = ref(false);

    function validate() {
      if (form.title === "") {
        form.titleErrorText = "یه چیز سرچ کن سید";
      }

      if (form.title !== "") {
        loading.value = true;
        createPost();
      }
    }

    function createPost() {
      axios
        .get(
          `https://one-api.ir/radiojavan/?token=391485:61f7f4d9433398.65027587&action=search&q=${form.title}`
        )
        .then(function (response) {
          loading.value = false;
          posts.value = response.data.result.mp3s;
          videos.value = response.data.result.videos;
          if (!response.data.result.mp3s.length) {
            notfound.value = true;
            found.value = false;
          } else {
            notfound.value = false;
            found.value = true;
          }
        })
        .catch(function (error) {
          console.log(error);
        });
    }

    return { form, validate, loading, posts, videos, notfound, found };
  },
};
</script>

