<template>
  <Header />

  <div v-if="IranUser">
    <div class="container">
      <div class="row">
        <div
          class="col-md-12 mt-5"
          style="text-align: center; font-weight: bold"
        >
          <img
            src="../public/images/undraw_two_factor_authentication_namy.svg"
            style="width: 50%"
          />
          <h2 class="mt-5">دسترسی مقدور نیست</h2>
          <p style="mt-2">
            دسترسی به سایت به دلیل فیلتر بودن سرویس رادیوجوان میسر نیست
          </p>
          <p style="mt-1">
           راه حل : استفاده از نرم افزار های تغییر ای پی
          </p>
        </div>
      </div>
    </div>
  </div>

  <router-view v-else :key="$route.path"></router-view>
</template>

<script>
import Header from "./components/sections/Header.vue";
import { ref } from "vue";

export default {
  name: "App",
  components: {
    Header,
  },

  setup() {
    const Userip = ref([]);
    const userloc = ref(false);
    const IranUser = ref(false);

    function myUserip() {
      fetch("https://api.ipify.org?format=json")
        .then((response) => response.json())
        .then((response) => {
          Userip.value = response.ip;
        });
    }

    function myUserloc() {
      fetch(`http://ip-api.com/json/${Userip.value}`)
        .then((response) => response.json())
        .then((response) => {
          userloc.value = response.country;
          if (userloc.value == "Iran") {
            IranUser.value = true;
          }
        });
    }
    myUserloc();
    myUserip();
    return { Userip, IranUser };
  },
};
</script>

